body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background: url(/Tic-Tac-Toe-React-Redux/static/media/background.bbdab570.jpg) no-repeat center center fixed;
}

.App {
  text-align: center;
}

.App-logo {
  width: 35px;
  height: 35px;
  padding-left: 6px;
  padding-right: 6px;
}

.choice-container, .winner-container {
  width: 310px;
  height: 310px;
  border: solid 2px black;
  margin: 0 auto;  
}

.winner-container {
  position: relative;
  top: 18%;
  background-color: rgb(102,51,153, 0.7);
  outline: none;
}

h3 {
  font-size: 25px;
}

.choice-container {
  background-color: white;
}

.winner {
  margin-top: 35%;
  text-align: center;
  font-size: 40px;
  color: navy;
}

.choice-question {
  position: relative;
  top: 35%;
  -webkit-user-select: none; /* Chrome/Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+ */
  /* Rules below not implemented in browsers yet */
  -o-user-select: none;
  user-select: none;
}

.board-container {
  width: 350px;
  height: 310px;
  margin: 0 auto;
  display: grid;
  margin-bottom: 30px;
}

.row {
  display: inline-flex;
  margin: 0 auto;
}

.cell {
  width: 100px;
  height: 100px;
  font-size: 70px;
  border: solid 2px black;
  background-color: white;
}

.cell:hover {
  background-color: darkturquoise;  
}

button {
  border: solid 1px black;
  padding: 5px 15px 5px 15px;
  margin-left: 5px;
  margin-right: 5px;
  background-color: white;
  font-weight: bold;
  font-size: 25px;
}

button:hover {
  cursor: pointer;
  background-color: darkturquoise;
}

.resetButton {
  width: 80px;
  margin: 0 auto;
  margin-bottom: 20px;
  font-size: 15px;
  margin-left: 35%;
}

footer {
  padding: 5%;
}

.builtWith {
  font-weight: bold;
}

.fa-github {
  color: whitesmoke;
}

.fa-4x {
  color: black;
}

.fa-github:hover {
  color: rgb(85, 26, 139);
}

.github-profile-link {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  font-weight: bold;
}

.github-profile-link:hover {
  color: blue;
}
@media only screen and (min-width: 320px) and (max-width: 425px){
    .board-container {
        width: 100vw;
    }


}

@media only screen and (min-width: 320px){
    .winner-container {
        top: 18%;
    }

    .builtWith {
        display: flex;
        align-items: center;
        width: 293px;
        margin: 0 auto;
        margin-top: 0;
        margin-bottom: 10%;        
    }

}

@media only screen and (min-width: 768px) {
    h1 {
        font-size: 45px;
    }

    h3 {
        font-size: 35px;
    }

    .choice-container, .winner-container {
        top: 25%;
        width: 462px;
        height: 462px;
    }

    .winner-container {
        font-size: 55px;
    }

    .winner {
        margin-bottom: 0;        
    }

    .resetButton {
        width: 140px;
        height: 50px;
        font-size: 23px;
    }

    .board-container {
        width: auto;
        height: auto;
    }

    .cell {
        width: 150px;
        height: 150px;
        font-size: 100px;
    }

    .builtWith {
        margin-top: 10%;
        margin-bottom: 10%;
    }


}

@media only screen and (min-width: 768px) {
    .builtWith {
        margin-top: 0;
        margin-bottom: 10%;
    }    
}
